.container {
  max-width: 600px;
  margin: 0 auto;
}
#money,
#final_score {
  text-align: center;
  font-size: 5em;
}
#money.small-1,
#final_score.small-1 {
  font-size: 4.5em;
}
#money.small-2,
#final_score.small-2 {
  font-size: 4em;
}
#money.small-3,
#final_score.small-3 {
  font-size: 3.5em;
}
#money.small-4,
#final_score.small-4 {
  font-size: 3em;
}
#time {
  text-align: center;
  font-size: 1.5em;
}
.btn {
  width: 90%;
  height: 80px;
  margin: 8px auto;
  text-align: center;
  line-height: 80px;
  border-radius: 10px;
}
@media (max-width: 780px) {
  #btn__holder {
    position: fixed;
    left: 0;
    bottom: 0;
  }
}
#btn__holder {
  width: 100%;
}
.btn {
  background-color: green;
}
.btn.disabled {
  background-color: grey;
}
.screen--gameOver {
  display: none;
}

#title {
  text-align: center;
}

.gameOver .screen--play {
  display: none;
}
.gameOver .screen--gameOver {
  display: block;
}

* {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: "Titan One", cursive;
}
.font-reg {
  font-family: Arial, Helvetica, sans-serif;
}
